import React from 'react';

const Finished = () => (
    <div style={{margin: '200px 500px', padding: '10px 30px 35px 30px', backgroundColor: '#cfcfd1', boxShadow: '1px 1px 1px 0px rgba(0,0,0,0.75)'}}>
                                <div style={{paddingBottom: '10px'}}>  
                                    <h3><strong>Thank you for the responses!</strong></h3>
                                    <p>Thank you for taking the time to fill out the responses. After we read over your work, 
                                        someone will be in touch to let you know if we would like to proceed any further.
                                    </p>
                                    </div>
                            </div>
);

export default Finished;


